import {
    Component,
    Input,
    Output,
    EventEmitter,
    OnInit,
    ElementRef,
    ViewChild,
    AfterViewInit,
    ChangeDetectionStrategy,
    OnDestroy,
} from '@angular/core';
import { Observable, Subject, merge } from 'rxjs';
import { debounceTime, distinctUntilChanged, switchMap, takeUntil } from 'rxjs/operators';

import { AddressService } from '@services/modules/services/address-service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { SearchAddress, SearchConfigOrder } from '@schemas/modules/core/search-address';
import { FormGroup } from '@angular/forms';
import { ScreenDetectorService } from '@services/screen-detector.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { TzFocusType } from '@core/constants/keyboard-key-types.const';
import { DialogService } from '@services/dialog.service';

const defaultPlacement = 'bottom-left | bottom-right | top-left | top-right';

@Component({
    selector: 'tz-loc-field',
    templateUrl: './loc-field.component.html',
    styleUrls: ['./loc-field.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LocFieldComponent implements OnInit, AfterViewInit, OnDestroy {
    @Input() htmlConf: any;
    @Input() formGroup = new FormGroup({});
    @Input() searchData: any;
    @Input() formField!: SearchConfigOrder;
    @Input() readonly = false;
    @Input() editMode = false;
    @Output() locSelected = new EventEmitter<{ selectedObj: any; formField: any }>();
    @Output() invertFromTo = new EventEmitter<void>();
    @Output() searchDropDownToggle: EventEmitter<boolean> = new EventEmitter<boolean>();
    @ViewChild('locField') fieldRef!: ElementRef<HTMLInputElement>;

    public dropdownPlacement = defaultPlacement;
    public fieldConf: any;
    focus$ = new Subject<string>();

    private inputValue = '';
    private locationSelected = false;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private addressService: AddressService,
        private screenSizeDetectorService: ScreenDetectorService,
        private listenersService: ListenersService,
        private dialogService: DialogService
    ) {}

    get showInvertBtn(): boolean {
        return (
            this.formField.field === 'to' &&
            this.fieldConf &&
            this.fieldConf.mainOrder === 2 &&
            this.searchData.from?.displayStr &&
            this.searchData.to.displayStr
        );
    }

    private static getNewInput(displayStr: string, selectedStr: string): string | undefined {
        for (let i = 0; i < selectedStr.length; i++) {
            if (selectedStr[i] !== displayStr[i]) {
                return displayStr[i];
            }
        }
        return undefined;
    }

    ngOnInit(): void {
        this.fieldConf = this.htmlConf[this.formField.confIdx][this.formField.field];
        this.screenSizeDetectorService.isMobile.subscribe((isMobile) => {
            if (isMobile) {
                this.dropdownPlacement = this.formField.fieldOrder % 2 === 0 ? 'bottom-right' : 'bottom-left';
            } else {
                this.dropdownPlacement = this.formField.fieldOrder > 2 ? 'bottom-right' : 'bottom-left';
            }
        });
    }

    ngAfterViewInit(): void {
        if (this.searchData[this.formField.field] && this.searchData[this.formField.field].displayStr) {
            const displayStr = this.searchData[this.formField.field].displayStr;
            this.fieldRef.nativeElement.value = displayStr;
            this.inputValue = displayStr;
        } else {
            this.fieldRef.nativeElement.value = '';
            this.inputValue = '';
        }

        const field = this.formGroup.controls[this.formField.field];
        field?.valueChanges?.subscribe(() => {
            if (!field?.value) {
                return;
            }

            if (field.value?.displayStr && field.value.displayStr !== '') {
                this.inputValue = field.value?.displayStr;
            }
        });
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public search = (text$: Observable<string>): Observable<any> => {
        const inputFocus$ = this.focus$;
        const debouncedText$ = text$.pipe(debounceTime(300), distinctUntilChanged());

        return merge(debouncedText$, inputFocus$).pipe(
            switchMap((term) =>
                this.addressService.getTypeAheadList(term, this.formField, this.fieldConf, this.searchData)
            )
        );
    };

    public invertFieldVals(): void {
        this.invertFromTo.emit();
    }

    public onInputFocus(): void {
        this.searchDropDownToggle.emit(true);
    }

    // To clear input value on blur if it's not been selected from dropdown
    public clearInputTxt(clear: boolean): void {
        this.searchDropDownToggle.emit(false);

        this.fieldRef.nativeElement.dispatchEvent(
            new KeyboardEvent('keydown', {
                keyCode: 27,
                bubbles: true,
            })
        );

        if (this.locationSelected) {
            return;
        }

        const selectedData = this.searchData[this.formField.field];
        if (clear || !selectedData || !selectedData.placeId || selectedData.placeId === '') {
            this.fieldRef.nativeElement.value = '';
            this.updateSearch({
                selectedObj: SearchDataService.getNewLoc(),
                formField: this.formField,
            });
        }

        if (clear) {
            this.fieldRef.nativeElement.click();
            this.fieldRef.nativeElement.focus();
        }

        if ((this.editMode || this.fieldRef.nativeElement.value !== '') && !clear) {
            this.fieldRef.nativeElement.value = this.inputValue;
        }
    }

    public itemSelected(selectedObj: { item: any; preventDefault: any }): void {
        this.locationSelected = true;
        if (selectedObj.item.isMyLocation) {
            const updateUserLocation = (address: SearchAddress) => {
                this.inputValue = address.displayStr;
                this.updateSearch({
                    selectedObj: address,
                    formField: this.formField,
                });

                requestAnimationFrame(() => {
                    this.formGroup.controls[this.formField.field].setValue(address);
                    this.focusNextField();
                });
            };

            if (selectedObj.item.placeId) {
                updateUserLocation(selectedObj.item);
                return;
            }

            this.addressService
                .checkGeoLocationAllowed()
                .pipe(takeUntil(this.destroy$))
                .subscribe(
                    (currentLocationInfo) => {
                        updateUserLocation(currentLocationInfo);
                    },
                    (error) => {
                        this.formGroup.controls[this.formField.field].reset();
                        this.dialogService.openSnackBar({ message: error });
                        console.log('Error while fetching current location: ', error);
                    }
                );

            return;
        }

        const isCachedAddress = selectedObj.item.isCached;
        this.addressService.formSelectedAddress(selectedObj.item, this.htmlConf, this.fieldConf, this.searchData).then(
            (res: any) => {
                this.updateLocData(selectedObj, res);
                this.inputValue = res.displayStr;
                this.addressService.cacheSelectedLocation(
                    { ...selectedObj.item, ...res },
                    this.formField,
                    this.fieldConf
                );

                if (!isCachedAddress) {
                    requestAnimationFrame(() => {
                        this.focusNextField();
                    });
                }
            },
            (res: any) => {
                this.updateLocData(selectedObj, res);
                this.locationSelected = false;
                console.log('Fail: ', res);
            }
        );

        if (isCachedAddress) {
            this.focusNextField();
        }
    }

    // TO format uib-data once selected
    public formatter(obj: { displayStr: string } | string): string {
        if (typeof obj === 'string') {
            return obj;
        } else if (obj.displayStr) {
            return obj.displayStr;
        } else {
            return '';
        }
    }

    //Reset already selected search data when we try to edit
    public resetOnNewInput(): void {
        const fieldVal = this.fieldRef.nativeElement.value;
        const selectedData = this.searchData[this.formField.field];

        if (!selectedData || !selectedData.selectedStr || fieldVal.trim() === selectedData.selectedStr.trim()) {
            return;
        }

        let input = '';
        const clearSelectedAddress = fieldVal.length > selectedData.selectedStr.length;
        if (clearSelectedAddress) {
            if (selectedData.selectedStr === fieldVal.substring(0, selectedData.selectedStr.length)) {
                input = fieldVal[fieldVal.length - 1];
            } else {
                const newInput = LocFieldComponent.getNewInput(fieldVal, selectedData.selectedStr);
                if (newInput) {
                    input = newInput;
                } else {
                    return;
                }
            }
        } else if (fieldVal.length < 3) {
            input = fieldVal;
        } else if (fieldVal.length <= selectedData.selectedStr.length) {
            return;
        }

        const newLoc = SearchDataService.getNewLoc();
        newLoc.displayStr = input;
        this.updateSearch({
            selectedObj: newLoc,
            formField: this.formField,
        });

        this.inputValue = input;
        this.fieldRef.nativeElement.value = this.inputValue;
    }

    private updateSearch(obj: { selectedObj: any; formField: any }): void {
        if (!obj.selectedObj.placeId || obj.selectedObj.placeId === '') {
            this.formGroup.controls[this.formField.field].reset();
        }

        this.locSelected.emit(obj);
    }

    private updateLocData(selectedObj: { item: any; preventDefault: any }, formedAddr: any): void {
        if (!formedAddr) {
            selectedObj.preventDefault();
            formedAddr = SearchDataService.getNewLoc();
        }

        formedAddr.selectedStr = formedAddr && formedAddr.displayStr ? formedAddr.displayStr : '';
        this.updateSearch({
            selectedObj: formedAddr,
            formField: this.formField,
        });
    }

    private focusNextField(): void {
        this.listenersService.NotifyListeners(ListenerId.LISTEN_FOCUS_NEXT, {
            id: 'tabIdx' + this.formField.tabOrder,
            moveTo: TzFocusType.MOVE_NEXT,
        });
        this.locationSelected = false;
    }
}
