import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root',
})
export class SearchState {
    private searchState: any;
    private passengers: any;

    public init(initialState: any) {
        this.searchState = initialState;
    }

    public saveState(fieldName: string, data: any) {
        if (this.searchState[fieldName]) {
            this.searchState[fieldName] = data;
        }
    }

    public getState(fieldName: string) {
        if (fieldName) {
            return this.searchState[fieldName];
        }
        return this.searchState;
    }

    public setPassengers(passengers: any): any {
        this.passengers = passengers;
    }

    public getPassengers(): any {
        return this.passengers;
    }
}
