import { Injectable } from '@angular/core';
import { Trip } from '@core/constants/trip';
import { CalTypes } from '@core/constants/call-types.const';

@Injectable({
    providedIn: 'root',
})
export class CalendarUtilsService {
    constructor() {}

    public static isTwoDates(
        dateRange: boolean | string,
        travelType: Trip | string,
        hasAcc?: boolean
    ): boolean | undefined {
        switch (dateRange) {
            case CalTypes.CAL_RANGE_TRAVEL:
                return hasAcc || travelType === Trip.ROUND_TRIP;
            case CalTypes.CAL_RANGE_FREE:
                return travelType ? travelType === Trip.ROUND_TRIP : undefined;
            case CalTypes.CAL_RANGE_ONE_DATE:
                return false;
            case CalTypes.CAL_RANGE_TWO_DATES:
                return true;
            default:
                return;
        }
    }

    public static monthDiff(d1: Date, d2: Date) {
        let months;
        months = (d2.getFullYear() - d1.getFullYear()) * 12;
        months -= d1.getMonth();
        months += d2.getMonth();
        return months <= 0 ? 0 : months;
    }

    public static getMonthAndYear(monthNumber: number, year: number) {
        return {
            monthNumber,
            year,
        };
    }

    public static todayComparison(year: any, m: any, day: any): boolean {
        const todayDate = new Date();
        const formDateIns = new Date(year, m, day);
        const nextYearDate = new Date(todayDate.getFullYear() + 1, todayDate.getMonth(), todayDate.getDate());

        if (formDateIns > nextYearDate) {
            return true;
        }

        return new Date(todayDate.getFullYear(), todayDate.getMonth(), todayDate.getDate()) > formDateIns;
    }

    public static isValidDate(date: any): boolean {
        return date instanceof Date && !isNaN(date.getTime());
    }

    public static isTheSameDaySelectedForAccommodation(tripCal: string, searchConf: any, calData: any) {
        return (
            calData.toDate &&
            calData.toDate !== '' &&
            CalendarUtilsService.isSelectedDateRangeCheck('tripCal', searchConf, calData)
        );
    }

    public static isSelectedDateRangeCheck(calType: string, searchConf: any, calData: any): boolean {
        let resetDate = false;
        for (let confIdx = searchConf.length - 1; confIdx >= 0; confIdx--) {
            for (const field in searchConf[confIdx]) {
                if (field !== calType) {
                    continue;
                }
                const fromDate = new Date(calData.fromDate).getTime();
                const toDate = new Date(calData.toDate).getTime();
                resetDate =
                    Math.floor((toDate - fromDate) / 86400000) > searchConf[confIdx][calType].maxDays ||
                    toDate === fromDate;
            }
        }
        return resetDate;
    }

    public static shouldResetTransferCalendar(transferCal: any, tripCal: any, selectedServices: any): boolean {
        if (selectedServices.accommodation && selectedServices.transfer && tripCal.toDate && tripCal.toDate !== '') {
            if (
                transferCal &&
                transferCal.tripType === Trip.ONE_WAY &&
                transferCal.fromDate &&
                transferCal.fromDate !== ''
            ) {
                return (
                    new Date(tripCal.fromDate) > new Date(transferCal.fromDate) ||
                    new Date(transferCal.fromDate) > new Date(tripCal.toDate)
                );
            } else if (transferCal && transferCal.toDate && transferCal.toDate !== '') {
                return (
                    new Date(tripCal.fromDate) > new Date(transferCal.fromDate) ||
                    new Date(transferCal.toDate) > new Date(tripCal.toDate)
                );
            }
        }

        return false;
    }

    public static shouldResetPreselectedDates(calType: string, htmlSearchConf: any, search: any): boolean {
        if (!htmlSearchConf) {
            return false;
        }
        for (let confIdx = htmlSearchConf.length - 1; confIdx >= 0; confIdx--) {
            for (const field in htmlSearchConf[confIdx]) {
                if (field !== calType || !htmlSearchConf[confIdx][field].linked) {
                    continue;
                }

                let minDate;
                let maxDate;

                if (
                    htmlSearchConf[confIdx][field].linked.fromDate &&
                    search[htmlSearchConf[confIdx][field].linked.fromDate] &&
                    search[htmlSearchConf[confIdx][field].linked.fromDate].fromDate !== ''
                ) {
                    minDate = new Date(search[htmlSearchConf[confIdx][field].linked.fromDate].fromDate);
                }

                if (
                    htmlSearchConf[confIdx][field].linked.toDate &&
                    search[htmlSearchConf[confIdx][field].linked.toDate] &&
                    search[htmlSearchConf[confIdx][field].linked.toDate].toDate !== ''
                ) {
                    maxDate = new Date(search[htmlSearchConf[confIdx][field].linked.toDate].toDate);
                }

                if (
                    (minDate && minDate > new Date(search[calType].fromDate)) ||
                    (maxDate && maxDate < new Date(search[calType].toDate))
                ) {
                    return true;
                }
            }
        }
        return false;
    }

    public static getDefaultCalendarValue(keepOpen = false): any {
        return {
            fromDate: '',
            toDate: '',
            showCalendar: keepOpen,
            updateTime: false,
            fromDisplayStr: '',
            toDisplayStr: '',
            businessMeeting: false,
            preferBC: false,
            translator: false,
            travelOption: 'roundTrip',
            displayStr: '',
        };
    }
}
