    public disableToMM: any;
    public disableToHHInc: any;
    public disableToMMInc: any;
    public disableFromHHInc: any;
    public disableFromMMInc: any;
    public disableFromHH: any;
    public disableFromMM: any;
    public isHoverValidDate: any;
    public isModalEditMode: boolean | undefined;

