import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { SearchAddress } from '@schemas/modules/core/search-address';

@Component({
    selector: 'tz-mob-loc-field-results',
    templateUrl: './mob-loc-field-results.component.html',
    styleUrls: ['./mob-loc-field-results.component.scss'],
})
export class MobLocFieldResultsComponent implements OnInit {
    @Input() results: SearchAddress[] = [];
    @Input() text = '';
    @Input() isInputFocused!: boolean;
    @Input() inputValue!: string;
    @Input() fieldName!: string;
    @Output() selectItem: EventEmitter<SearchAddress> = new EventEmitter<SearchAddress>();

    constructor() {}

    ngOnInit() {}

    public onSelectItem(item: SearchAddress): void {
        this.selectItem.emit(item);
    }
}
