import { Directive, ElementRef, EventEmitter, HostListener, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { KeyType, TzFocusType } from '@core/constants/keyboard-key-types.const';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';

const fieldTypes = {
    ADDR_TYPE: 'loc-field',
    CAL_TYPE: 'cal',
    PERSON_TYPE: 'person',
    SEARCH_TYPE: 'searchButton',
    OPTIONAL: 'optionalfield',
};
const ID_PREFIX = '#tabIdx';
const SEARCH_BUTTON_IDX = 9;
type focusHandler = { id: string; checkCurrentField: boolean; moveTo: number };

@Directive({
    selector: '[tzFieldNavigation]',
})
export class FieldNavigationDirective implements OnInit, OnDestroy {
    @Input() tabindex!: number;
    @Input() searchFormGroup = new FormGroup({});
    @Input() controllerName = '';
    @Input() fieldType = '';
    @Input() ignoreEnterKey = false;

    @Output() closeField = new EventEmitter<void>();

    private keyBoardEvent!: KeyboardEvent;
    private focusNextListenerId = -1;
    private currentField!: HTMLElement;

    constructor(private EleRef: ElementRef, private listenersService: ListenersService) {
        this.currentField = this.EleRef.nativeElement as HTMLElement;
    }

    @HostListener('document:keydown', ['$event'])
    onKeyUp(event: KeyboardEvent) {
        this.keyBoardEvent = event;

        const targetElement = (this.keyBoardEvent.target || this.keyBoardEvent.currentTarget) as HTMLElement;
        this.handleKeyBoardEvent(event.key, {
            id: targetElement.id,
        } as focusHandler);
    }

    ngOnInit(): void {
        this.focusNextListenerId = this.listenersService.registerListener(
            ListenerId.LISTEN_FOCUS_NEXT,
            this.moveNext,
            this
        );
    }

    ngOnDestroy(): void {
        if (this.focusNextListenerId !== -1) {
            this.listenersService.unregisterListener(this.focusNextListenerId);
        }
    }

    private getNextField(nextIndex: number, inc: number): HTMLElement {
        const nextInput = document.querySelector(ID_PREFIX + nextIndex) as HTMLElement;

        if (nextInput && nextInput.getAttribute(fieldTypes.OPTIONAL) === 'true') {
            // next element is optional and empty let's move to next one
            return this.getNextField(nextIndex + inc, inc);
        }

        if (!nextInput) {
            if (nextIndex >= SEARCH_BUTTON_IDX) {
                // move to search button with id = ID_PREFIX + SEARCH_BUTTON_IDX
                return document.querySelector(ID_PREFIX + SEARCH_BUTTON_IDX) as HTMLElement;
            } else if (nextIndex !== 0) {
                return this.getNextField(nextIndex + inc, inc);
            }
        }

        return nextInput;
    }

    //close current field popup
    private closeCurrentField(): void {
        requestAnimationFrame(() => {
            this.closeField.emit();
            this.currentField.blur();
            this.currentField.dispatchEvent(
                new KeyboardEvent('keydown', {
                    keyCode: 27,
                    bubbles: true,
                })
            );
        });
    }

    private getNextTabIndex(keyType: string | number): number | undefined {
        let inc = 0;
        const locationSelected = (): boolean => {
            const fieldValue = this.searchFormGroup.controls[this.controllerName].value;
            return typeof fieldValue === 'object' || fieldValue === '';
        };

        switch (keyType) {
            case KeyType.ENTER:
                if (this.ignoreEnterKey) {
                    return;
                }

                if (this.tabindex === SEARCH_BUTTON_IDX) {
                    // sendEvent(el, 'click');
                    return;
                } else {
                    inc = 1;
                }
                break;

            case KeyType.RIGHT_ARROW:
                if (this.fieldType === fieldTypes.ADDR_TYPE && !locationSelected()) {
                    return;
                }
            /* fallthrough */
            case TzFocusType.MOVE_NEXT:
                inc = 1;
                break;

            case KeyType.LEFT_ARROW:
                if (this.fieldType === fieldTypes.ADDR_TYPE && !locationSelected()) {
                    return;
                }

                inc = -1;
                break;

            case KeyType.TAB:
                if (this.keyBoardEvent.shiftKey) {
                    inc = -1;
                } else {
                    inc = 1;
                }
                break;

            case KeyType.ESCAPE:
                this.closeCurrentField();
                return;

            case TzFocusType.MOVE_TO_INVALID:
                break;

            default:
                return;
        }

        return inc;
    }

    private handleKeyBoardEvent(keyType: string | number, handlerObj: focusHandler): void {
        const isCurrentElement = handlerObj.id === this.currentField.id;
        const hasTabIndex = this.tabindex;
        if (!isCurrentElement || !hasTabIndex) {
            return;
        }

        const inc = this.getNextTabIndex(keyType);
        if (inc === undefined) {
            return;
        }

        const nextFieldIndex = this.tabindex + inc;
        let nextField = this.getNextField(nextFieldIndex, inc);
        if (!nextField) {
            return;
        }

        this.closeCurrentField();

        const focusSearchBtn =
            this.fieldType === fieldTypes.PERSON_TYPE &&
            this.searchFormGroup.valid &&
            nextField.id === 'tabIdx' + SEARCH_BUTTON_IDX;
        if (focusSearchBtn) {
            requestAnimationFrame(() => {
                nextField.focus();
            });
            return;
        }

        if (this.fieldType === fieldTypes.CAL_TYPE && keyType === KeyType.ENTER) {
            keyType = TzFocusType.MOVE_TO_INVALID;
            handlerObj.checkCurrentField = true;
        }

        if (keyType === TzFocusType.MOVE_NEXT || keyType === TzFocusType.MOVE_TO_INVALID) {
            const listenerObj = {
                id: nextField.id,
                checkCurrentField: true,
            };

            if (handlerObj?.checkCurrentField) {
                const isCurrentFieldValid =
                    (this.fieldType === fieldTypes.PERSON_TYPE && this.searchFormGroup.valid) ||
                    (this.fieldType !== fieldTypes.PERSON_TYPE &&
                        this.searchFormGroup.controls[this.controllerName].valid); /*&&
                        (this.fieldType !== fieldTypes.ADDR_TYPE ||
                            (this.fieldType === fieldTypes.ADDR_TYPE &&
                                typeof this.searchFormGroup.controls[this.controllerName].value === 'object'))*/
                if (isCurrentFieldValid) {
                    this.listenersService.NotifyListeners(ListenerId.LISTEN_FOCUS_NEXT, listenerObj);
                    return;
                }

                nextField = this.currentField;
            } else {
                this.listenersService.NotifyListeners(ListenerId.LISTEN_FOCUS_NEXT, listenerObj);
                return;
            }
        }

        requestAnimationFrame(() => {
            nextField.click();
            nextField.focus();
        });
    }

    private moveNext(handlerObj: focusHandler) {
        if (!handlerObj || this.currentField.id !== handlerObj.id) {
            return;
        }

        this.handleKeyBoardEvent(
            handlerObj.moveTo === TzFocusType.MOVE_TO_INVALID ? TzFocusType.MOVE_TO_INVALID : TzFocusType.MOVE_NEXT,
            handlerObj
        );
    }
}
