import { Injector } from '@angular/core';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { PlatformService } from '@services/platform.service';
import { SettingsService } from '@services/services/settings-service';
import { DatesService } from '@services/modules/search/utils/dates.service';

export class BaseTrazlerCalendar {
    protected searchDataService: SearchDataService;
    protected datesService: DatesService;
    protected listenersService: ListenersService;
    protected platformService: PlatformService;
    protected settingsService: SettingsService;

    constructor(injector: Injector) {
        this.searchDataService = injector.get(SearchDataService);
        this.datesService = injector.get(DatesService);
        this.listenersService = injector.get(ListenersService);
        this.platformService = injector.get(PlatformService);
        this.settingsService = injector.get(SettingsService);
    }
}
