import {
    AfterViewInit,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    SimpleChanges,
} from '@angular/core';
import { Trip } from '@core/constants/trip';
import * as moment from 'moment';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { SettingsService } from '@services/services/settings-service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ModalService } from '@services/modal.service';
import { TrazlerCalendarComponent } from '../trazler-calendar/components/trazler-calendar/trazler-calendar.component';
import { PlatformService } from '@services/platform.service';
import { CalendarUtilsService } from '@services/modules/search/utils/calendar.utils';
import { DatesService } from '@services/modules/search/utils/dates.service';
import { CalTypes } from '@core/constants/call-types.const';

@Component({
    selector: 'tz-calendar',
    templateUrl: './calendar.component.html',
    styleUrls: ['./calendar.component.scss'],
})
export class CalendarComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
    @Input() formGroup: any;
    @Input() searchData: any;
    @Input() calData = {
        fromDate: '',
        toDate: '',
        fromTime: '',
        toTime: '',
        travelOption: 'roundTrip',
        twoDates: true,
        showCalendar: false,
    };
    @Input() fieldConf: any;
    @Input() formField!: {
        field: string;
        confIdx: number;
        fieldType: string;
        tabOrder: number;
    };
    @Input() readonly = false;
    @Input() isModalEditMode = false;
    @Input() travelType: string = Trip.ROUND_TRIP;
    @Input() selectedServices!: { [key: string]: boolean };
    @Output() dateSelected = new EventEmitter<{ selectedObj: any; formField: any }>();
    @Output() focusInvalid = new EventEmitter<void>();
    @Output() searchDropDownToggle: EventEmitter<boolean> = new EventEmitter<boolean>();

    public minDate = moment(new Date(), 'DD-MM-YYYY');
    // public maxDate = moment(new Date(this.today.getFullYear() + 1, this.today.getMonth(), this.today.getDate()), 'DD-MM-YYYY');
    public showTime = false;
    public locale = {
        format: 'YYYY-MM-DDTHH:mm:ss.SSSSZ',
        displayFormat: 'DD MMM YYYY',
        applyLabel: 'Done', // detault is 'Apply'
        clearLabel: 'Clear', // detault is 'Clear'
    };
    public destroy$: Subject<boolean> = new Subject<boolean>();
    private calTypes = CalTypes;

    constructor(
        private datesService: DatesService,
        private searchDataService: SearchDataService,
        private settingsService: SettingsService,
        private modalService: ModalService,
        public platformService: PlatformService
    ) {}

    ngAfterViewInit() {
        this.patchValueOfTheCalendarInput(this.calData);
    }

    ngOnInit(): void {
        this.showTime = this.fieldConf.time;
        if (this.showTime) {
            this.locale.displayFormat = 'DD MMM HH:mm';
        }
        this.initDefaultDates();
        this.settingsService.selectedLanguageSubject.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.patchValueOfTheCalendarInput(this.calData);
        });
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes['calData'] && changes['calData'].currentValue) {
            if (changes['calData'].currentValue) {
                this.patchValueOfTheCalendarInput(this.calData);
            }
        }
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onCalendarFieldFocus(field: string) {
        if (this.platformService.isMobileDevice) {
            const content = this.fieldConf.content;
            let modalCssClass = this.isModalEditMode ? ['mob-second-search-modal'] : ['mob-main-modal'];
            let modalTitle = 'SEARCH.TRIP_DATES';

            if (content === this.calTypes.CAL_TRANSFER_TYPE || content === this.calTypes.CAL_RENTAL_TYPE) {
                modalCssClass = ['mob-main-modal', 'mob-picker-modal'];
                modalTitle = 'SEARCH.SELECT_DATE_AND_TIME';
            }

            const data = {
                travelOption: this.travelType,
                fieldConf: this.fieldConf,
                searchData: this.searchData,
                defaultValue: this.searchData[this.formField.field],
                calendarData: this.searchData[this.formField.field],
                selectedServices: this.selectedServices,
                isModalEditMode: this.isModalEditMode,
            };

            if (content === this.calTypes.CAL_TRANSFER_TYPE || content === this.calTypes.CAL_RENTAL_TYPE) {
                this.modalService
                    .openDatePickerModal(TrazlerCalendarComponent, {
                        data,
                        title: modalTitle,
                        cssClass: modalCssClass,
                    })
                    .then((modal: any) => {
                        if (modal.data) {
                            this.onApplyCalendarData({ newDates: modal.data.newDates, keepOpen: false });
                        }
                    });
            } else {
                this.modalService
                    .createMainModal(TrazlerCalendarComponent, {
                        data,
                        title: modalTitle,
                        cssClass: modalCssClass,
                    })
                    .then((modal: any) => {
                        if (modal.data) {
                            this.onApplyCalendarData({ newDates: modal.data.newDates, keepOpen: false });
                        }
                    });
            }
        } else {
            this.searchData[field].showCalendar = true; //!this.searchData[field].showCalendar;
            this.searchDropDownToggle.emit(true);
        }
    }

    public onCalendarFieldBlur() {
        this.searchDropDownToggle.emit(false);
        this.searchData[this.formField.field].showCalendar = false;
    }

    public onApplyCalendarData($event: any): void {
        const { keepOpen, newDates } = $event;
        this.calData.showCalendar = keepOpen;
        this.searchData[this.formField.field].showCalendar = true;
        newDates.showCalendar = keepOpen;
        if (!newDates.fromDate) {
            this.calData.fromDate = '';
            this.calData.toDate = '';
            this.setDefaultCal();
            this.setFieldInvalid();
        } else {
            this.calData = newDates;
        }
        this.dateSelected.emit({ selectedObj: this.calData, formField: this.formField });
        this.patchValueOfTheCalendarInput(newDates);
        if (!keepOpen) {
            this.searchData[this.formField.field].showCalendar = true;
            this.focusInvalid.emit();
        }
    }

    public ngModelChange(event: any): void {
        if (!event.startDate) {
            this.calData.fromDate = '';
            this.calData.toDate = '';
            this.setDefaultCal();
            this.setFieldInvalid();
        } else {
            const twoDatesRequired = this.calData.twoDates || this.fieldConf.dateRange === true;
            this.calData.fromDate = event.startDate.format('YYYY-MM-DD');
            if (twoDatesRequired) {
                this.calData.toDate = event.endDate.format('YYYY-MM-DD');
            }

            if (this.showTime) {
                this.calData.fromTime = event.startDate.format('HH:mm');

                if (twoDatesRequired) {
                    this.calData.toTime = event.endDate.format('HH:mm');
                }
            }
        }
        this.dateSelected.emit({ selectedObj: this.calData, formField: this.formField });
        this.focusInvalid.emit();
    }

    public onClear() {
        this.formGroup.controls[this.formField.field].reset();
        this.resetSearch(this.formField.field);
    }

    private setDefaultCal(): void {
        let defaultTime = '';

        this.calData.travelOption = this.travelType;
        this.calData.twoDates = this.travelType !== Trip.ONE_WAY;

        if (this.fieldConf.defaultTime) {
            defaultTime = this.fieldConf.defaultTime + ':00';
            this.calData.fromTime = this.calData.fromTime ? this.calData.fromTime : defaultTime;
            this.calData.toTime = this.calData.toTime ? this.calData.toTime : defaultTime;
        }
        this.patchValueOfTheCalendarInput(this.calData);
    }

    private initDefaultDates(): void {
        this.setDefaultCal();
        if (
            this.searchData[this.formField.field] &&
            (this.searchData[this.formField.field].fromDate || this.searchData[this.formField.field].toDate)
        ) {
            this.calData = { ...this.calData, ...this.searchData[this.formField.field] };
        } else {
            if (!this.showTime) {
                this.setDefaultCal();
            }
        }
    }

    private patchValueOfTheCalendarInput(calData: any): void {
        const control = this.formGroup.controls[this.formField.field];
        const twoDates = CalendarUtilsService.isTwoDates(
            this.fieldConf.dateRange,
            this.travelType,
            this.searchDataService.hasAcc()
        );
        const transferCal = this.fieldConf.content === this.calTypes.CAL_TRANSFER_TYPE;
        const transferRoundTrip = transferCal && calData.tripType === Trip.ROUND_TRIP;

        if (
            ((!transferCal && twoDates) || transferRoundTrip) &&
            !calData.toDate &&
            control.validator !== null /*&&
            ((calData.tripType !== undefined && calData.tripType !== Trip.ONE_WAY) ||
                (calData.tripType === undefined && this.travelType !== Trip.ONE_WAY))*/
        ) {
            this.setFieldInvalid();
        } else {
            const data: any = {};
            data[this.formField.field] = this.dateString(calData);
            this.formGroup.patchValue(data);
        }
    }

    private resetSearch(str: string): void {
        this.searchData[str] = {
            fromDate: '',
            toDate: '',
            showCalendar: true,
            updateTime: false,
            fromDisplayStr: '',
            toDisplayStr: '',
            businessMeeting: false,
            preferBC: false,
            translator: false,
            travelOption: 'roundTrip',
            displayStr: '',
        };
    }

    private setFieldInvalid(): void {
        queueMicrotask(() => {
            this.formGroup.controls[this.formField.field].setErrors({ invalid: true });
        });
    }

    private dateString(calData: any): string {
        return this.datesService.getCalendarDateString(
            calData.fromDate,
            calData.toDate,
            calData.diplayStr,
            this.fieldConf,
            this.calData.fromTime,
            this.calData.toTime,
            this.travelType
        );
    }
}
