import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Injector,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    SimpleChanges,
} from '@angular/core';
import { Router } from '@angular/router';
import { GeneralSettings } from '@core/constants/general-settings';
import { ServiceId } from '@core/constants/service-id.const';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { DateRangePipe } from '@shared/pipes/date-range.pipe';
import { SearchInfoInterface } from '@schemas/modules/search/search';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DatePipe, Location } from '@angular/common';
import { TzCheckoutService } from '@services/modules/checkout/services/tz-checkout.service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { StateFrom } from '@core/constants/state-from.const';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { CurrencyService } from '@services/services/currency-service';
import { MobPickerWheels } from '@schemas/modules/picker/mob-picker';
import { ModalService } from '@services/modal.service';
import { Currency } from '@schemas/modules/core/currency';
import { AdjustedRentalCalendar } from '@schemas/modules/car-rentals/car-rentals-response';

@Component({
    selector: 'tz-mob-search-details',
    templateUrl: './mob-search-details.component.html',
    styleUrls: ['./mob-search-details.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobSearchDetailsComponent implements OnChanges, OnDestroy, OnInit {
    @Input() selectedServices: { [key: string]: boolean } = GeneralSettings.defaultSelectedServices;
    @Input() isHotelDetailsPage = false;
    @Input() currentService: ServiceId = ServiceId.INVALID;
    @Output() editSearch: EventEmitter<void> = new EventEmitter<void>();

    public searchInfo!: SearchInfoInterface;
    public accommodation: undefined | CheckoutCartServiceSummary;
    public currency!: string;
    public serviceId = ServiceId;
    private destroy$: Subject<boolean> = new Subject<boolean>();
    private dateRange: DateRangePipe;
    private currencies!: Currency[];
    private selectedCurrency!: Currency;

    constructor(
        private changeDetectorRef: ChangeDetectorRef,
        private searchDataService: SearchDataService,
        injector: Injector,
        private router: Router,
        public datePipe: DatePipe,
        private tzCheckoutService: TzCheckoutService,
        private cartService: CartService,
        private cartRouterService: CartRoutingService,
        private currencyService: CurrencyService,
        private modalService: ModalService,
        private location: Location
    ) {
        this.dateRange = injector.get(DateRangePipe);

        this.searchDataService.isUpdatedSearch$.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.serviceTitle(this.currentService);
            this.updateAccommodationStatus();
        });
    }

    public ngOnInit(): void {
        this.serviceTitle(this.currentService);

        this.getCurrencies();
        this.currencyChange();
    }

    public ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.serviceTitle(this.currentService);
    }

    public insertTime = (date: string, st: string = '', et: string = '') =>
        date.replace(new RegExp('–', 'g'), st + ' –') + ' ' + et;

    public serviceTitle(serviceId: ServiceId): void {
        this.changeDetectorRef.markForCheck();

        const { searchData, passengers } = this.searchDataService.getSearch(serviceId);

        if (!searchData) {
            return;
        }

        switch (serviceId) {
            case ServiceId.FLIGHT_TRAIN: {
                this.searchInfo = {
                    to: searchData.to.displayStr,
                    from: searchData.from?.displayStr,
                    adults: passengers.adults,
                    children: passengers.children + passengers.infants,
                    people: passengers.children ? passengers.children + passengers.adults : passengers.adults,
                    rooms: passengers.rooms,
                    ...this.getDateTransform(searchData.tripCal.fromDate, searchData.tripCal.toDate),
                };

                break;
            }
            case ServiceId.ACC: {
                this.searchInfo = {
                    to: searchData.address.displayStr,
                    adults: passengers.adults,
                    children: passengers.children + passengers.infants,
                    people: passengers.total,
                    rooms: passengers.rooms,
                    ...this.getDateTransform(searchData.tripCal.fromDate, searchData.tripCal.toDate),
                };

                break;
            }
            case ServiceId.TRANSFER: {
                this.searchInfo = {
                    to: searchData.address?.displayStr,
                    from: searchData.to?.displayStr,
                    adults: passengers.adults,
                    children: passengers.children + passengers.infants,
                    people: passengers.children ? passengers.children + passengers.adults : passengers.adults,
                    rooms: passengers.rooms,
                    ...this.getDateTransform(searchData.transferCal.fromDate, searchData.transferCal.toDate),
                };

                break;
            }
            case ServiceId.CAR_RENTAL: {
                this.searchInfo = {
                    from: searchData.to?.displayStr,
                    to: searchData.address?.displayStr,
                    adults: passengers.adults,
                    people: passengers.children ? passengers.children + passengers.adults : passengers.adults,
                    children: passengers.children + passengers.infants,
                    rooms: passengers.rooms,
                };

                if (searchData.rentalCal) {
                    this.searchInfo = {
                        ...this.searchInfo,
                        ...this.getCarRentalDate(searchData.rentalCal),
                    };
                }
                break;
            }
        }
    }

    public gotoRoute(): void {
        if (this.currentService === ServiceId.ACC_DETAILS) {
            this.cartRouterService.goToState(ServiceId.ACC, undefined, false, StateFrom.ACC_DETAILS);
            return;
        }

        this.location.back();
    }

    public onEditSearch(): void {
        this.editSearch.emit();
    }

    public onOpenCurrencyPicker(): void {
        const wheels: MobPickerWheels[] = [];

        this.currencies.map((lang) => {
            wheels.push({ display: lang.name, value: lang.code });
        });

        const selected = this.selectedCurrency.code;

        this.modalService.openPickerModal({ wheels, selected: [selected] }, {}).then((modal) => {
            if (modal.data?.selected) {
                this.currencyService.setCurrency(modal.data.selected[0]);
            }
        });
    }

    private getDateTransform(fromDate: string, toDate?: string, st?: string, et?: string) {
        if (toDate && st && et) {
            return {
                dateRange: `${this.datePipe.transform(fromDate + 'T' + st, 'd MMM HH:mm')} - ${
                    this.datePipe.transform(toDate + 'T' + et, 'MMM') ===
                    this.datePipe.transform(fromDate + 'T' + st, 'MMM')
                        ? this.datePipe.transform(toDate + 'T' + et, 'd MMM HH:mm')
                        : this.datePipe.transform(toDate + 'T' + et, 'd MMM HH:mm')
                }`,
            };
        } else if (toDate) {
            return {
                dateRange:
                    this.datePipe.transform(fromDate, 'MMM') === this.datePipe.transform(toDate, 'MMM')
                        ? `${this.datePipe.transform(fromDate, 'd')} - ${this.datePipe.transform(toDate, 'd MMM yyyy')}`
                        : `${this.datePipe.transform(fromDate, 'd MMM')} - ${this.datePipe.transform(
                              toDate,
                              'd MMM yyyy'
                          )}`,
            };
        } else if (fromDate && st) {
            return {
                dateRange: `${this.datePipe.transform(fromDate + 'T' + st, 'd MMM yyyy HH:mm')}`,
            };
        } else {
            return { date: this.insertTime(fromDate, st, et) };
        }
    }

    private getCarRentalDate(rentalCal: AdjustedRentalCalendar): { dateRange: string } {
        const fromDate = this.datePipe.transform(rentalCal.fromDate + 'T' + rentalCal.fromTime, 'd MMM HH:mm');
        const toDate = this.datePipe.transform(rentalCal.toDate + 'T' + rentalCal.toTime, 'd MMM YYYY HH:mm');

        return { dateRange: `${fromDate} - ${toDate}` };
    }

    private updateAccommodationStatus(): void {
        const cartData = this.cartService.getCartData();
        if (cartData) {
            const cartSummaryItems = this.tzCheckoutService.formCheckoutCartSummary(cartData.items);

            this.accommodation = cartSummaryItems.find(
                (selectedCart: CheckoutCartServiceSummary) => selectedCart.serviceId === ServiceId.ACC
            );
        }
    }

    private getCurrencies(): void {
        this.currencies = this.currencyService.currencies;
    }

    private currencyChange(): void {
        this.currencyService.selectedCurrencySubject.subscribe((currency) => {
            this.currency = currency.selectedLable;
            this.selectedCurrency = currency;
            this.changeDetectorRef.detectChanges();
        });
    }
}
