import {
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    Renderer2,
    SimpleChanges,
} from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';

import { ISearchStatus, UserSearchService } from '@services/searchEngine/services/user-search-service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { EngineRuleService, IValidSearch } from '@services/searchEngine/services/engine-rule-service';
import { ToolsService } from '@core/utils/tools-service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { SearchHelpers } from '@services/searchEngine/services/search-helpers';
import { SearchState } from '@services/searchEngine/services/search-state.service';
import { ServiceId } from '@core/constants/service-id.const';
import { Trip, TRIPTYPES } from '@core/constants/trip';
import { AddressService } from '@services/modules/services/address-service';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { LoaderService } from '@services/modules/core/loader.service';
import { AddressSource, SearchConfigOrder } from '@schemas/modules/core/search-address';
import { GeneralSettings } from '@core/constants/general-settings';
import { PassengerReq } from '@shared/schemas/tz-service-search-data';
import { Subject } from 'rxjs';
import { TzPassengerUtil } from '@core/utils/tz-passenger.util';
import { CartService } from '@services/modules/cart/services/cart.service';
import { TzFocusType } from '@core/constants/keyboard-key-types.const';
import { ISearchData } from '@schemas/modules/search/search';
import { RecentSearchService } from '@services/modules/services/recent-search.service';
import { Router } from '@angular/router';
import { SearchUtilsService } from '@services/modules/search/utils/search.utils';
import { takeUntil } from 'rxjs/operators';
import { PlatformService } from '@services/platform.service';
import { CalTypes } from '@core/constants/call-types.const';
import { ModalService } from '@services/modal.service';
import { CalendarUtilsService } from '@services/modules/search/utils/calendar.utils';

@Component({
    selector: 'tz-search',
    templateUrl: './search.component.html',
    styleUrls: ['./search.component.scss', './search.component.tablet.scss'],
    //changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SearchComponent implements OnInit, OnDestroy, OnChanges {
    @Input() selectedServices: { [key: string]: boolean } = GeneralSettings.defaultSelectedServices;
    @Input() editMode = false;
    @Input() editing = false;
    @Input() currentService: ServiceId = ServiceId.INVALID;
    @Output() editingChange: EventEmitter<boolean> = new EventEmitter<boolean>();
    @Output() editingClose: EventEmitter<void> = new EventEmitter<void>();
    @Output() searchDropDownToggle: EventEmitter<boolean> = new EventEmitter<boolean>();

    public htmlSearchConf: any;
    public formOrder!: SearchConfigOrder[];
    public searchFormGroup: FormGroup;
    public search: any;
    public oldSearch!: ISearchData;
    public context!: {
        services: number[];
        travelType: string;
        runningMode: number;
    };
    public passengers!: PassengerReq;
    public oldPassenger!: PassengerReq;
    public fieldLength = 0;
    public serviceIcon = '';
    public trips = TRIPTYPES.LIST;
    public passengersFormGroup = new FormGroup({});
    public classList = [
        { label: 'SEARCH.CLASS.Economy' },
        { label: 'SEARCH.CLASS.Premium Economy' },
        { label: 'SEARCH.CLASS.Business' },
        { label: 'SEARCH.CLASS.First' },
    ];
    public ratingList = [
        { rating: 1, selected: false },
        { rating: 2, selected: false },
        { rating: 3, selected: false },
        { rating: 4, selected: false },
        { rating: 5, selected: false },
    ];
    public resetPassengersValue = 0;
    public data:
        | {
              selectedServices: { [key: string]: boolean };
              editMode: boolean;
              editing: boolean;
              currentService: ServiceId;
          }
        | undefined;
    public isModalEditMode = false;

    private fieldOrderType = '';
    private listeners: number[] = [];
    private formValues!: { [key: string]: any };
    private passengersFormValues!: { childAges: { childAge: number }[] };
    private passengersOldValue!: PassengerReq;
    private isOldPassengerDataSet = false;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private userSearchService: UserSearchService,
        private toolsService: ToolsService,
        private searchDataService: SearchDataService,
        private listenersService: ListenersService,
        private engineRuleService: EngineRuleService,
        private searchState: SearchState,
        private fb: FormBuilder,
        private addressService: AddressService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private loaderService: LoaderService,
        private cartService: CartService,
        private recentSearchService: RecentSearchService,
        private router: Router,
        private renderer: Renderer2,
        public platformService: PlatformService,
        private modalService: ModalService
    ) {
        this.searchFormGroup = new FormGroup({});
    }

    get childAgesFormArray(): FormArray {
        return this.passengersFormGroup.controls['childAges'] as FormArray;
    }

    ngOnInit(): void {
        if (this.platformService.isMobileDevice && this.data) {
            this.selectedServices = this.data.selectedServices;
            this.editMode = this.data.editMode;
            this.editing = this.data.editing;
            this.currentService = this.data.currentService;
            this.isModalEditMode = true;
        }

        this.initializeTzSearch();

        this.searchDataService.resetSearch$.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.initializeTzSearch();
        });
    }

    ngOnChanges(changes: SimpleChanges): void {
        const currentService = changes['currentService'];
        const resultPageSwitched =
            currentService &&
            !currentService.firstChange &&
            currentService.currentValue !== currentService.previousValue;
        if (resultPageSwitched) {
            this.unregisterListeners();
            this.initializeTzSearch();
        }

        const editing = changes['editing'];
        if (editing && !editing.firstChange && !editing.currentValue) {
            this.resetSearch();
            /* if (editing.currentValue.serviceId) {
                let isValid = this.engineRuleService.isValidSearch(this.search, this.passengers, this.selectedServices,
                 this.context.travelType, this.editMode);
                editing.currentValue.invalid = !isValid.isValid;
                this.searchErrorService.searchErrHdl();
                return
            }*/
        }

        if (changes['currentService'] && !changes['currentService'].firstChange) {
            this.isOldPassengerDataSet = false;
        }
    }

    // TODO: update hotel's code in params while edit from details page

    public onSearch(): void {
        this.renderer.removeClass(document.body, 'body-lock-scroll');

        if (this.searchFormGroup.invalid || this.passengersFormGroup.invalid) {
            if (this.searchFormGroup.valid && this.passengersFormGroup.invalid) {
                this.passengersFormGroup.markAllAsTouched();
            }

            this.focusInvalid();
            return;
        }

        const formValidity = this.isFormInvalid();
        if (!formValidity.isValid) {
            if (formValidity.firstField) {
                this.focusInvalid();
            }
            return;
        }

        const isNewSearch = SearchUtilsService.isNewSearch(
            this.searchDataService.getSearch(
                SearchHelpers.getFirstServiceId(SearchHelpers.getServiceValue(this.selectedServices))
            ),
            this.search,
            this.passengers,
            this.selectedServices,
            this.context.travelType,
            this.editMode
        );
        if (!isNewSearch) {
            if (this.editMode) {
                this.editingClose.emit();

                if (this.platformService.isMobileDevice && this.data) {
                    this.modalService.dismiss();
                }
            } else {
                this.editing = true;
            }
            return;
        }
        this.closeAllCalendars();
        //this.editingChange.emit(false);
        this.searchDropDownToggle.emit(false);
        const params = this.getSearchParams();
        const searchReq = {
            searchData: ToolsService.deepCopy(this.search),
            passengers: ToolsService.deepCopy(this.passengers),
            context: {
                ...this.context,
                services: SearchHelpers.getServiceValue(this.selectedServices),
            },
        };
        if (params && params.hotelCode !== '') {
            searchReq.searchData.hotelCode = params.hotelCode;
            searchReq.searchData.hotelName = params.hotelName;
        }
        this.recentSearchService.storeRecentAddress(searchReq, this.selectedServices, this.context.travelType);

        if (!this.editMode) {
            SearchUtilsService.resetSortingValue();

            this.loaderService.triggerLoading.emit({ loading: true, textKey: 'LOADER_MESSAGES.SEARCH_OPTIONS' });

            this.cartService.clearCart().subscribe(
                () => {
                    this.makeSearch(params, searchReq);
                },
                (error: any) => {
                    this.loaderService.triggerLoading.emit({ loading: false });

                    if (this.platformService.isMobileDevice && this.data) {
                        this.modalService.dismiss();
                    }

                    console.log(error);
                }
            );
        } else {
            this.makeSearch(params, searchReq);
        }
    }

    public invertFromTo(): void {
        const selectedFrom = this.search.from;
        const selectedTo = this.search.to;

        this.search.from = { ...selectedTo, inputName: selectedFrom.inputName };
        this.search.to = { ...selectedFrom, inputName: selectedTo.inputName };

        this.searchFormGroup.controls['from'].setValue(this.search.from);
        this.searchFormGroup.controls['to'].setValue(this.search.to);

        this.recentSearchService.setRecentAddress(this.search.from, false, 'type2');
        this.recentSearchService.setRecentAddress(this.search.to, false, 'type2');

        if (this.search.address) {
            this.search.address = SearchDataService.getNewLoc();
            this.searchFormGroup.controls['address'].reset();
        }
    }

    public applySearchData(fieldEvent: { selectedObj: any; formField: any }): void {
        this.search = {
            ...this.search,
            [fieldEvent.formField.field]: fieldEvent.selectedObj,
        };

        if (fieldEvent.formField.fieldType === 'loc-field' && fieldEvent.selectedObj?.latitude) {
            this.cleanLinkedField(fieldEvent.formField.field);
        }

        if (fieldEvent.formField.fieldType === 'cal' && fieldEvent.formField.field === CalTypes.CAL_TRIP_TYPE) {
            this.checkIfSecondaryCalendarsShouldBeReset();
        }
    }

    public applyPassenger(paxEvent: { passenger: PassengerReq }): void {
        this.passengers = paxEvent.passenger;
        if (!this.isOldPassengerDataSet) {
            this.oldPassenger = ToolsService.deepCopy(this.passengers);
            this.isOldPassengerDataSet = true;
        }
    }

    public isRatingSelected(): boolean {
        return this.ratingList.some((rating) => rating.selected);
    }

    public changeTravelType(travelType: { label: string; tripType: Trip }): void {
        if (travelType.tripType === Trip.MULTI_CITY) {
            this.context.travelType = Trip.ROUND_TRIP;
            return;
        }
        requestAnimationFrame(() => {
            if (!this.platformService.isMobileDevice) {
                this.focusInvalid();
            }
        });
        this.context.travelType = travelType.tripType;
        this.searchDataService.changeFlyType({ travelType: travelType.tripType });
    }

    public addNewChild(age: number): void {
        const childAgeCtrl = new FormControl(age >= 0 ? age : -1);
        childAgeCtrl.setValidators([
            Validators.required,
            Validators.min(0),
            Validators.max(GeneralSettings.passengerRules.childMaxAge),
        ]);

        this.childAgesFormArray.push(
            this.fb.group({
                childAge: childAgeCtrl,
            })
        );
    }

    public focusInvalid(moveToInvalid = true): void {
        this.listenersService.NotifyListeners(ListenerId.LISTEN_FOCUS_NEXT, {
            id: 'tabIdx1',
            moveTo: moveToInvalid ? TzFocusType.MOVE_TO_INVALID : TzFocusType.MOVE_NEXT,
        });
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
        this.unregisterListeners();
    }

    private isFormInvalid(): IValidSearch {
        return EngineRuleService.validSearch(
            this.search,
            this.passengers,
            this.selectedServices,
            this.context.travelType,
            this.editMode
        );
    }

    private unregisterListeners(): void {
        for (const listenerId of this.listeners) {
            this.listenersService.unregisterListener(listenerId);
        }
    }

    private makeSearch(params: any, searchReq: any) {
        this.userSearchService.search(params, searchReq).then(
            (searchStatus: ISearchStatus) => {
                this.searchResHandler(searchStatus);
            },
            (searchStatus: ISearchStatus) => {
                this.searchResHandler(searchStatus);
            }
        );
    }

    private searchResHandler(searchStatus: ISearchStatus): void {
        if (this.editMode && this.editing) {
            console.log('cancel editing');
            this.editing = false;
            this.editingClose.emit();

            if (this.platformService.isMobileDevice) {
                this.modalService.dismiss();
            }
        }
        if (searchStatus.needEditRefresh) {
            this.resetSearch();
        }

        if (searchStatus.searchInProgress) {
            console.log('set old values to current one');
            this.formValues = this.searchFormGroup.value;
            this.passengersFormValues = this.passengersFormGroup.value;
            this.passengersOldValue = ToolsService.deepCopy(ToolsService.deepCopy(this.passengers));
            this.oldSearch = ToolsService.deepCopy(this.search);
            this.oldPassenger = ToolsService.deepCopy(this.passengers);
        } else if (!this.editMode) {
            this.editing = true;
        }
    }

    private setSearchToCurrent(currentService: ServiceId | undefined): boolean {
        if (currentService === undefined || currentService === ServiceId.INVALID) {
            console.error(`invalid service id: ${currentService}`);
            return false;
        }
        console.log('setting current search content');
        const serviceInfo = SearchUtilsService.getServiceInfo(currentService);
        const search = this.searchDataService.getSearch(currentService);
        if (serviceInfo) {
            this.selectedServices = serviceInfo.service;
            this.serviceIcon = serviceInfo.icon;
        }

        this.search = search.searchData;
        this.oldSearch = ToolsService.deepCopy(this.search);
        // console.log({searchData: this.search})

        this.context = search.context;
        // if (this.disableTransferController) {
        // modifyTransferCal(search);
        // }
        // hdlNullCalInfo(this.tabView);
        this.passengers = search.passengers;
        this.passengersOldValue = ToolsService.deepCopy(search.passengers);
        this.passengers.isDefault = false;
        this.passengers.displayStr = '';
        if (serviceInfo && serviceInfo.service.rentCar) {
            this.passengers.isRental = true;
        }

        return true;
    }

    private initEditSearch(): void {
        this.unregisterListeners();
        if (!this.setSearchToCurrent(this.currentService)) {
            return;
        }
        this.setListener(
            //used to refresh edit search content with fresh data (called by trip listener)
            this.listenersService.registerListener(ListenerId.LISTEN_EDIT_EXT_CHANGE, this.initEditSearch, this)
        );
        this.searchState.setPassengers(this.passengers);
        // ListenersService.NotifyListeners(ListenersService.LISTEN_PASSENGER_RESET, SearchState.getPassengers());
        // const displayTxt = ''; //tzPassengerService.GetEditPassengerDisplay(serviceInfo.service, search.passengers, true);

        //initialise the invalid status on search fields fix for TRAZ-6614
        EngineRuleService.validSearch(
            this.search,
            this.passengers,
            this.selectedServices,
            this.context.travelType,
            this.editMode
        );
        // _getCompleteStatus();
        this.searchState.init(this.search);
        this.buildSearchForm();
    }

    private initializeTzSearch(): void {
        this.editingChange.emit(false);
        this.serviceIcon = '';
        this.fieldOrderType = this.editMode ? 'editOrder' : 'mainOrder';

        if (this.editMode) {
            this.initEditSearch();
            return;
        }
        this.unregisterListeners();
        const search = ToolsService.deepCopy(this.searchDataService.getEmptySearch());
        this.search = search.searchData;
        this.oldSearch = ToolsService.deepCopy(this.search);
        this.context = search.context;
        this.passengers = TzPassengerUtil.FormPax(this.selectedServices, search.passengers, this.translateService);
        this.oldPassenger = ToolsService.deepCopy(this.passengers);
        this.buildSearchForm();

        this.addressService.checkGeoLocationAllowed().pipe(takeUntil(this.destroy$)).subscribe();
        this.setListener(
            this.listenersService.registerListener(ListenerId.LISTEN_SERVICES, this.updateFieldConfig, this)
        );
        this.setListener(
            this.listenersService.registerListener(ListenerId.LISTEN_TRAVEL_OPTION, this.updateFieldConfig, this)
        );
        this.setListener(
            this.listenersService.registerListener(ListenerId.LISTEN_SERVICE_REINIT, this.resetSearchToDefault, this)
        );
    }

    private setListener(listenerId: number): void {
        if (this.listeners.indexOf(listenerId) > -1) {
            return;
        }

        this.listeners.push(listenerId);
    }

    // _isAccDetailEdit is to validate to stay in accommodation details
    private isAccDetailEdit(): string | undefined {
        // Check whether we are in accommodation details page
        if (this.router.url.indexOf('/hotel/') !== 0) {
            return '';
        }
        let hotelCode = this.router.url.split('/').pop();

        // Already we are validating isNewSearch. so check the new search contains address change.
        // if no address change stay in the details page.
        const combinationParams = EngineRuleService.buildSearchFieldsObj(
            this.selectedServices,
            this.context.travelType
        );
        const tempSearch = this.searchDataService.getSearch(this.currentService);
        const resultSearch = tempSearch.searchData;
        const search = this.search;

        for (const i of combinationParams) {
            for (const k in combinationParams[i]) {
                if (k === 'address') {
                    // if change in address no need to stay in details page
                    if (search[k]) {
                        if (
                            search[k].uniqueKey &&
                            search[k].uniqueKey !== '' &&
                            search[k].uniqueKey !== resultSearch[k].uniqueKey
                        ) {
                            hotelCode = '';
                        } else if (
                            search[k].placeId &&
                            search[k].placeId !== '' &&
                            search[k].placeId !== resultSearch[k].placeId
                        ) {
                            hotelCode = '';
                        }
                    }
                    return hotelCode;
                }
            }
        }
        return hotelCode;
    }

    private getSearchParams(): any {
        const params = {
            bookedServiceId: undefined,
            editMode: this.editMode,
            serviceId: this.currentService === ServiceId.ACC_DETAILS ? ServiceId.ACC : this.currentService,
        };

        const hotelCode = this.isAccDetailEdit();
        if (hotelCode) {
            return {
                ...params,
                hotelCode: this.router.url.split('/')[2],
                hotelName: decodeURI(this.router.url.split('/')[4]),
            };
        }

        return params;
    }

    private resetCalendar(field: string): void {
        this.search[field] = {
            fromDate: null,
            toDate: null,
            fromTime: '00:00',
            toTime: '00:00',
            showCalendar: false,
        };
    }

    private closeAllCalendars() {
        if (this.search[CalTypes.CAL_RENTAL_TYPE]) {
            this.search[CalTypes.CAL_RENTAL_TYPE].showCalendar = false;
        }
        if (this.search[CalTypes.CAL_TRANSFER_TYPE]) {
            this.search[CalTypes.CAL_TRANSFER_TYPE].showCalendar = false;
        }
        if (this.search[CalTypes.CAL_TRIP_TYPE]) {
            this.search[CalTypes.CAL_TRIP_TYPE].showCalendar = false;
        }
    }

    private buildFormGroup(fieldOrders: SearchConfigOrder[], selectedServiceConf: any): { [key: string]: any } {
        const formGroup: { [key: string]: any } = {};
        let fieldCount = 0;

        for (const fieldOrder of fieldOrders) {
            if (!fieldOrder) {
                continue;
            }
            const conf = selectedServiceConf[fieldOrder.confIdx][fieldOrder.field];

            if (fieldOrder.fieldType === 'cal') {
                if (!this.search[fieldOrder.field]) {
                    this.resetCalendar(fieldOrder.field);
                }

                formGroup[fieldOrder.field] =
                    conf.optional && (!this.editMode || !conf.editMandatory) ? '' : ['', Validators.required];
            } else {
                const dispStr =
                    this.search[fieldOrder.field] && this.search[fieldOrder.field].displayStr
                        ? this.search[fieldOrder.field].displayStr
                        : '';
                formGroup[fieldOrder.field] =
                    conf.optional && (!this.editMode || !conf.editMandatory) ? dispStr : [dispStr, Validators.required];
            }

            fieldCount++;
        }

        this.fieldLength = fieldCount;
        return formGroup;
    }

    private buildSearchForm(): void {
        const selectedServiceConf = EngineRuleService.buildSearchFieldsObj(
            this.selectedServices,
            this.context.travelType
        );
        const fieldOrders = SearchDataService.formSearchFieldOrder(
            selectedServiceConf,
            this.fieldOrderType,
            this.editMode
        );

        this.searchFormGroup = this.fb.group(this.buildFormGroup(fieldOrders, selectedServiceConf));

        this.formOrder = fieldOrders;
        this.htmlSearchConf = selectedServiceConf;
        this.buildPassengerFormGroup();

        this.formValues = this.searchFormGroup.value;
        this.passengersFormValues = this.passengersFormGroup.value;
    }

    private updateFieldConfig(data: any, subj: number): void {
        switch (subj) {
            case ListenerId.LISTEN_SERVICES:
                this.selectedServices = data;
                this.passengers = TzPassengerUtil.FormPax(
                    this.selectedServices,
                    this.passengers,
                    this.translateService
                );
                this.serviceChange(data);
                this.buildSearchForm();
                break;
            case ListenerId.LISTEN_TRAVEL_OPTION:
                this.context.travelType = data.travelType;
                if (data.runMode) {
                    this.context.runningMode = data.runMode;
                }

                this.buildSearchForm();
                break;
        }
    }

    private buildPassengerFormGroup(): void {
        const group: any = {
            childAges: this.fb.array([]),
        };
        const passengerConf = this.htmlSearchConf[1].persons;

        if (passengerConf.driver) {
            group.driversAge = new FormControl(this.passengers.driversAge > 0 ? this.passengers.driversAge : '');
            group.driversAge.setValidators([
                Validators.required,
                Validators.min(GeneralSettings.passengerRules.minDriverAge),
                Validators.max(GeneralSettings.passengerRules.maxDriverAge),
            ]);
        }
        this.passengersFormGroup = this.fb.group(group);

        if (!TzPassengerUtil.rentalOnly(this.selectedServices)) {
            const childAges = [
                ...this.passengers.infantAges,
                ...this.passengers.childrenAges,
                ...this.passengers.youthAges,
            ];
            for (const i of childAges) {
                this.addNewChild(i);
            }
        }
    }

    private notifyLocInconsistency(from: string, to: string, inconsistencyType: string): void {
        const conf = this.htmlSearchConf;
        for (const c of conf) {
            for (const fieldName in c) {
                if (!c.hasOwnProperty(fieldName)) {
                    continue;
                }

                if (fieldName === from) {
                    from = this.translateService.instant(c[fieldName].placeholder);
                } else if (fieldName === to) {
                    to = this.translateService.instant(c[fieldName].placeholder);
                }
            }
        }

        const formatAddress = (addr: string): string => {
            addr = addr.split(',')[0];
            addr = addr.split('(')[0];
            return addr;
        };

        this.dialogService.openSnackBar({
            message: this.translateService.instant(inconsistencyType, {
                from: formatAddress(from),
                to: formatAddress(to),
            }),
        });
    }

    private cleanLinkedField(field: string) {
        const search = this.search;
        const conf = this.htmlSearchConf;
        if (search[field].noChange) {
            return;
        }

        for (const c of conf) {
            for (const fieldName in c) {
                if (!c.hasOwnProperty(fieldName)) {
                    continue;
                }

                const hasLinkedField = c[fieldName]?.linked?.field === field && search[fieldName]?.latitude;
                if (!hasLinkedField) {
                    continue;
                }

                const dep = c[fieldName].linked;
                const distance = ToolsService.distance(
                    search[fieldName].latitude,
                    search[fieldName].longitude,
                    search[dep.field].latitude,
                    search[dep.field].longitude
                );
                if (isNaN(distance)) {
                    continue;
                }

                const addressLocTooFar = dep.radius && dep.radius > 0 && distance > dep.radius;
                const addressLocTooNear = dep.minRadius && dep.minRadius > 0 && distance < dep.minRadius;
                if (addressLocTooFar || addressLocTooNear) {
                    search[fieldName] = SearchDataService.getNewLoc();
                    this.searchFormGroup.controls[fieldName].reset();
                    this.notifyLocInconsistency(
                        field,
                        fieldName,
                        addressLocTooFar ? 'NOTIFS.FROM_TO_TOO_FAR' : 'NOTIFS.FROM_TO_TOO_NEAR'
                    );
                }
            }
        }
    }

    private validateSelectedAddress(): void {
        const searchData = this.search;

        const isFromValidAirportCity =
            searchData.from.placeId &&
            (!searchData.from.latitude ||
                !searchData.from.longitude ||
                (!searchData.from.isMyLocation && searchData.from.api_source !== AddressSource.SRC_AIRPORT_CITIES));
        if (isFromValidAirportCity) {
            this.search.from = SearchDataService.getNewLoc();
        }

        const isToValidAirportCity =
            searchData.to.placeId &&
            (!searchData.to.latitude || !searchData.to.longitude || searchData.to.api_source !== 'sac');
        if (isToValidAirportCity) {
            this.search.to = SearchDataService.getNewLoc();
        }
    }

    private serviceChange(data: any): void {
        if (data.transport) {
            this.validateSelectedAddress();
        } else if (!data.accommodation) {
            // reset from and to when single transfer or rental service selected
            this.search.from = SearchDataService.getNewLoc();
            this.search.to = SearchDataService.getNewLoc();
        }
        if (data.accommodation) {
            const shouldReset = CalendarUtilsService.isTheSameDaySelectedForAccommodation(
                'tripCal',
                this.htmlSearchConf,
                this.search.tripCal
            );
            if (shouldReset) {
                this.resetDate('tripCal');
            }
        }
    }

    private resetSearchToDefault(): void {
        const search = ToolsService.deepCopy(this.searchDataService.getEmptySearch());
        this.search = search.searchData;
        this.context = search.context;
        this.passengers = TzPassengerUtil.FormPax(this.selectedServices, search.passengers, this.translateService);
        this.context.travelType = Trip.ROUND_TRIP;
        this.searchFormGroup.reset();
    }

    private resetSearch() {
        if (this.currentService === undefined || this.currentService === ServiceId.INVALID) {
            return;
        }
        console.error('reset search');
        const search = ToolsService.deepCopy(this.searchDataService.getSearch(this.currentService));
        this.search = search.searchData;
        console.error(this.search);
        this.passengers = ToolsService.deepCopy(this.passengersOldValue);
        this.resetPassengersValue = ++this.resetPassengersValue;
        SearchUtilsService.clearPassengerFormArray(this.childAgesFormArray);
        this.passengersFormGroup.patchValue({ driversAge: this.passengersOldValue.driversAge });
        this.resetPassengerValues();
        this.searchFormGroup.reset({ ...this.formValues });
    }

    private resetPassengerValues(): void {
        this.passengersFormValues.childAges.map((childAge) => {
            this.childAgesFormArray.push(this.fb.group(childAge));
        });
    }

    private checkIfSecondaryCalendarsShouldBeReset() {
        if (
            CalendarUtilsService.shouldResetTransferCalendar(
                this.search.transferCal,
                this.search.tripCal,
                this.selectedServices
            )
        ) {
            this.resetDate('transferCal');
        }
        if (CalendarUtilsService.shouldResetPreselectedDates('rentalCal', this.htmlSearchConf, this.search)) {
            this.resetDate('rentalCal');
        }
    }

    private resetDate(field: string, keepOpen = false): void {
        this.search[field] = CalendarUtilsService.getDefaultCalendarValue();
    }
}
