import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import {
    AbstractControl,
    FormArray,
    FormControl,
    FormGroup,
    ValidationErrors,
    Validators,
    ValidatorFn,
} from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';

import { TzPassengerUtil } from '@core/utils/tz-passenger.util';
import { DefaultPassenger, PassengerReq } from '@shared/schemas/tz-service-search-data';
import { GeneralSettings } from '@core/constants/general-settings';
import { SettingsService } from '@services/services/settings-service';
import { skip, take, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { NgbDropdown } from '@ng-bootstrap/ng-bootstrap';
import { ServiceId } from '@core/constants/service-id.const';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { PlatformService } from '@services/platform.service';
import { ModalService } from '@services/modal.service';
import { MobPickerWheels } from '@schemas/modules/picker/mob-picker';

@Component({
    selector: 'tz-passenger',
    templateUrl: './passenger.component.html',
    styleUrls: ['./passenger.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PassengerComponent implements OnInit, OnChanges, OnDestroy {
    @ViewChild('passengerPopup', { read: NgbDropdown }) passengerPopup!: NgbDropdown;
    @ViewChild('driverAgeField', { read: ElementRef }) driverAge!: ElementRef;

    @Input() passenger!: PassengerReq;
    @Input() passengersFormGroup = new FormGroup({});
    @Input() editMode = false;
    @Input() isMobSearch = false;
    @Input() resetOldValue!: number;
    @Input() readonly = false;
    @Input() selectedServices: { [key: string]: boolean } = GeneralSettings.defaultSelectedServices;
    @Input() config!: {
        rooms: boolean;
        driver: boolean;
        tabOrder: number;
        driverOnly: boolean;
    };
    @Output() applyPax = new EventEmitter<{ passenger: PassengerReq }>();
    @Output() addNewChild = new EventEmitter<number>();
    @Output() focusInvalid = new EventEmitter<void>();
    @Output() searchDropDownToggle: EventEmitter<boolean> = new EventEmitter<boolean>();

    public passengerRules = GeneralSettings.passengerRules;
    public selectedPax!: DefaultPassenger;
    public childAgesList: {
        label: string;
        value: number;
    }[] = [];

    public data:
        | {
              passenger: PassengerReq;
              passengerAges: FormGroup;
              config: {
                  rooms: boolean;
                  driver: boolean;
                  tabOrder: number;
                  driverOnly: boolean;
              };
              selectedServices: { [key: string]: boolean };
              selectedPax: any;
              addNewChild$: Subject<number>;
              applyPax$: Subject<{ passenger: PassengerReq }>;
          }
        | undefined;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private translateService: TranslateService,
        private settingsService: SettingsService,
        private cartRoutingService: CartRoutingService,
        public platformService: PlatformService,
        private modalService: ModalService,
        private changeDetectorRef: ChangeDetectorRef
    ) {
        this.initiateChildrenDropdown();
        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            const passengerReq = TzPassengerUtil.MakePassengerReq(
                this.selectedServices,
                this.selectedPax,
                this.translateService
            );
            this.selectedPax.displayStr = passengerReq.displayStr;
            this.initiateChildrenDropdown();
        });
    }

    get childAges(): FormArray {
        return this.passengersFormGroup.controls['childAges'] as FormArray;
    }

    get dropdownPosition() {
        return this.editMode ? 'bottom-end' : 'bottom-start';
    }

    get isChildAgesValid(): boolean {
        let isFormValid = true;

        this.childAges.value.map((childAge: { childAge: number }) => {
            if (childAge.childAge === -1) {
                isFormValid = false;
            }
        });

        return isFormValid;
    }

    get mobSelectedPax(): string[] {
        return this.selectedPax.displayStr ? this.selectedPax.displayStr.split(',') : [];
    }

    ngOnInit() {
        this.mobSetInputData();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['config']) {
            this.selectedPax = TzPassengerUtil.FormPax(this.selectedServices, this.passenger, this.translateService);
            this.config.driverOnly = TzPassengerUtil.rentalOnly(this.selectedServices);
            this.reInitPassengersInfo();
            this.childAges.addValidators(this.infantsLimitValidator());
            if (!this.config.driverOnly) {
                this.passengersFormGroup.addControl('adults', new FormControl('', this.adultsLimitValidator()));
            } else {
                this.passengersFormGroup.removeControl('adults');
            }
        }

        if (changes['resetOldValue'] && changes['resetOldValue'].currentValue) {
            this.selectedPax = TzPassengerUtil.FormPax(this.selectedServices, this.passenger, this.translateService);
            this.applyPassengersInfo();
            this.childAges.addValidators(this.infantsLimitValidator());
        }

        if (changes['passengersFormGroup']) {
            this.passengersFormGroup?.statusChanges.subscribe((result) => {
                this.applyPassengersInfo();
            });
        }
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public addAdults(n: number): void {
        this.selectedPax.adults += n;
        const isMoreRoomsThanAdult = this.config.rooms && this.selectedPax.adults < this.selectedPax.rooms;
        if (isMoreRoomsThanAdult) {
            this.selectedPax.rooms = this.selectedPax.adults;
        }

        this.childAges.updateValueAndValidity();
        this.setTotal();
        this.passengersFormGroup.controls['adults'].updateValueAndValidity();
    }

    public addChild(n: number): void {
        this.selectedPax.child += n;
        if (n === 1) {
            this.addNewChild.emit(-1);

            if (this.platformService.isMobileDevice && this.data) {
                this.childAges.addValidators(this.infantsLimitValidator());
                this.data.addNewChild$.next(-1);
            }
        } else {
            const idx = this.childAges.length - 1;
            this.childAges.removeAt(idx);
        }

        this.setTotal();
    }

    public addRooms(n: number): void {
        this.selectedPax.rooms += n;
        this.selectedPax.isDefault = false;
        this.applyPassengersInfo();
    }

    public reInitPassengersInfo(dropdownClosed = false): void {
        const passengerReq = TzPassengerUtil.MakePassengerReq(
            this.selectedServices,
            this.selectedPax,
            this.translateService
        );

        this.selectedPax.displayStr = passengerReq.displayStr;

        this.applyPax.emit({ passenger: passengerReq });

        if (this.platformService.isMobileDevice && this.data) {
            this.data.applyPax$.next({ passenger: passengerReq });
        }

        if (dropdownClosed) {
            this.focusInvalid.emit();

            if (this.platformService.isMobileDevice) {
                this.modalService.dismiss();
            }
        }
    }

    public applyPassengersInfo(dropdownClosed = false): void {
        const ages = this.passengersFormGroup.value;
        this.selectedPax.driversAge = ages.driversAge;
        this.selectedPax.childAges = ages.childAges.map((ageObj: { childAge: number }) => ageObj.childAge);
        this.reInitPassengersInfo(dropdownClosed);
    }

    public isInValidField(controlName: string): boolean {
        const fieldControl = this.passengersFormGroup.controls[controlName];

        return (
            fieldControl.invalid &&
            (fieldControl.touched || fieldControl.dirty || (fieldControl.value !== null && fieldControl.value !== ''))
        );
    }

    public dropDownChange(isOpen: boolean): void {
        this.searchDropDownToggle.emit(isOpen);
        if (isOpen) {
            this.cartRoutingService.currentServiceId
                .pipe(skip(1), take(1), takeUntil(this.destroy$))
                .subscribe((serviceId: ServiceId) => {
                    this.passengerPopup.close();
                });

            requestAnimationFrame(() => {
                this.driverAge?.nativeElement.focus();
            });
        }
    }

    public onOpenPassengerModal(): void {
        const applyPax$: Subject<{ passenger: PassengerReq }> = new Subject<{ passenger: PassengerReq }>();
        const addNewChild$: Subject<number> = new Subject<number>();
        const cssClass = this.editMode ? 'mob-second-search-modal' : 'mob-main-modal';
        const data = {
            passenger: this.passenger,
            passengerAges: this.passengersFormGroup,
            config: this.config,
            selectedServices: this.selectedServices,
            selectedPax: this.selectedPax,
            applyPax$,
            addNewChild$,
        };

        addNewChild$.subscribe((data2: any) => {
            this.addNewChild.emit(data2);
            this.childAges.addValidators(this.infantsLimitValidator());
        });

        applyPax$.subscribe((data2: any) => {
            this.applyPax.emit(data2);
            this.childAges.addValidators(this.infantsLimitValidator());
        });

        this.modalService.createMainModal(PassengerComponent, { data, cssClass }).then(() => {
            this.applyPassengersInfo();
            this.childAges.addValidators(this.infantsLimitValidator());
        });
    }

    public onMobSelectChildAge(childAgesList: { value: number; label: string }[], index: number): void {
        const childAgeWheels: MobPickerWheels[] = [];

        childAgesList.map((childAge) => {
            if (childAge.value !== -1) {
                childAgeWheels.push({ display: childAge.label, value: childAge.value });
            }
        });

        const selectedChildAges = this.passengersFormGroup.controls['childAges'].value[index].childAge;

        const selected = selectedChildAges && selectedChildAges !== -1 ? selectedChildAges : childAgeWheels[0].value;
        this.modalService
            .openPickerModal({ wheels: childAgeWheels, selected: [selected] }, { title: 'PICKER.SELECT_CHILD_AGE' })
            .then((modal) => {
                if (!modal.data) {
                    return;
                }

                if (modal.data.selected) {
                    const childAges = this.passengersFormGroup.controls['childAges'] as FormArray;
                    childAges.controls[index].patchValue({ childAge: parseInt(modal.data.selected[0], 10) });
                    this.changeDetectorRef.detectChanges();
                }
            });
    }

    public onMobSelectDriverAge(): void {
        const minDriverAge = GeneralSettings.passengerRules.minDriverAge;
        const maxDriverAge = GeneralSettings.passengerRules.maxDriverAge;

        const driverAges: MobPickerWheels[] = [];

        for (let i = minDriverAge; i <= maxDriverAge; i++) {
            driverAges.push({ display: i, value: i });
        }

        const driverAge = this.passengersFormGroup.controls['driversAge'].value;
        const selected = driverAge ? driverAge : driverAges[0].value;

        this.modalService
            .openPickerModal({ wheels: driverAges, selected: [selected] }, { title: 'CAR_RENTAL.DRIVERS_AGE_LABEL' })
            .then((modal) => {
                if (!modal.data) {
                    return;
                }

                if (modal.data.selected) {
                    this.passengersFormGroup.controls['driversAge'].patchValue(parseInt(modal.data.selected[0], 10));
                    this.changeDetectorRef.detectChanges();
                }
            });
    }

    private setTotal(): void {
        this.selectedPax.total = this.selectedPax.adults + this.selectedPax.child;
        this.selectedPax.isDefault = false;
        this.applyPassengersInfo();
    }

    private adultsLimitValidator(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null =>
            this.selectedPax.adults <= 0 ? { Invalid: true } : null;
    }

    private infantsLimitValidator(): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            const totalInfants = control.value.filter((ageObj: { childAge: number }) => ageObj.childAge === 0);

            return this.selectedPax.adults < totalInfants.length ? { Invalid: true } : null;
        };
    }

    private initiateChildrenDropdown(): void {
        this.childAgesList = [
            { value: -1, label: this.translateService.instant('TRAVELLER.AGE_NEEDED') }, // non filled value
            { value: 0, label: this.translateService.instant('TRAVELLER.upto2') },
        ];

        for (let j = this.passengerRules.childMinAge; j <= this.passengerRules.childMaxAge; j++) {
            this.childAgesList.push({
                value: j,
                label: j.toString(),
            });
        }
    }

    private mobSetInputData(): void {
        if (this.platformService.isMobileDevice && this.data) {
            this.passenger = this.data.passenger;
            this.passengersFormGroup = this.data.passengerAges;
            this.config = this.data.config;
            this.selectedServices = this.data.selectedServices;
            this.selectedPax = this.data.selectedPax;

            this.data.passengerAges.valueChanges.subscribe((val) => {
                console.log('this.data.passengerAges');
                if (this.data) {
                    console.log(this.data.passengerAges);
                    this.childAges.addValidators(this.infantsLimitValidator());
                    console.log('this.childAges.valid');
                    console.log(this.childAges.valid);
                }
            });

            console.log('this.data.passengerAges.valid');
            console.log(this.data.passengerAges.valid);
        }
    }
}
