import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Trip } from '@core/constants/trip';
import { CalTypes } from '@core/constants/call-types.const';
import * as moment from 'moment';
import { ModalService } from '@services/modal.service';
import { MbscDatepicker } from '@mobiscroll/angular';
import { SettingsService } from '@services/services/settings-service';
import { localeEn, localeFr, localeEnGB } from '@mobiscroll/angular';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { CalendarConf } from '@core/constants/calendar-conf';

@Component({
    selector: 'tz-datetime-calendar',
    templateUrl: './datetime-calendar.component.html',
    styleUrls: ['./datetime-calendar.component.scss'],
})
export class DatetimeCalendarComponent implements OnInit, OnDestroy {
    @Input() calendarType!: string;
    @Input() search: any;
    @Input() defaultValue:
        | {
              displayStr: string;
              fromDate: string;
              fromTime: string;
              showCalendar: boolean;
              toDate: string | null;
              toDisplayStr: string;
              toTime: string | null;
              tripType: 'roundTrip' | 'oneWay';
              twoDates: boolean;
          }
        | undefined;
    @Input() travelOption: string | Trip = Trip.ONE_WAY;
    @Input() content = CalTypes.CAL_TRIP_TYPE;
    @Input() calendarConf: any = CalendarConf;

    public calendarDefaultValue: any = [new Date(), null];
    public locale = localeEn;
    public minDate: any = moment().add(10, 'minutes');
    public maxDate: any;

    private selectedDateTimeArr: Date[] | undefined;
    private calType = CalTypes;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(private modalService: ModalService, private settingsService: SettingsService) {}

    get isCarCalendar(): boolean {
        return this.calendarType === this.calType.CAL_RENTAL_TYPE;
    }

    get isTransferCalendar(): boolean {
        return this.calendarType === this.calType.CAL_TRANSFER_TYPE;
    }

    get startLabel(): string {
        if (this.isCarCalendar) {
            return 'GENERAL.PICK_UP_TIME';
        }

        return 'GENERAL.Depart';
    }

    get endLabel(): string {
        if (this.isCarCalendar) {
            return 'GENERAL.DROP_OFF_TIME';
        }

        return 'GENERAL.Return';
    }

    get isCalendarValueInvalid() {
        if (
            this.isCarCalendar &&
            this.selectedDateTimeArr &&
            this.selectedDateTimeArr[0] &&
            this.selectedDateTimeArr[1]
        ) {
            return false;
        }

        if (!this.isCarCalendar && this.selectedDateTimeArr && this.selectedDateTimeArr[0]) {
            if (
                !this.isCarCalendar &&
                this.selectedDateTimeArr &&
                this.minDate &&
                this.maxDate &&
                (!this.selectedDateTimeArr[0] || !this.selectedDateTimeArr[1])
            ) {
                return true;
            }

            return false;
        }

        return true;
    }

    ngOnInit() {
        this.setDefaultValue();
        this.langChange();
        this.setMinMaxDate();
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onChange(event: { inst: MbscDatepicker; value: Date[] }): void {
        this.selectedDateTimeArr = event.value;
    }

    public onSetDateTime(): void {
        if (!this.selectedDateTimeArr?.length || this.isCalendarValueInvalid) {
            return;
        }

        const fromDateTime = this.selectedDateTimeArr[0];
        const toDateTime = this.selectedDateTimeArr[1] ? this.selectedDateTimeArr[1] : null;

        const fromDateTimeMoment = moment(fromDateTime);
        const toDateTimeMoment = toDateTime ? moment(toDateTime) : null;
        const tripType = toDateTimeMoment ? 'roundTrip' : 'oneWay';

        const data = {
            displayStr: fromDateTimeMoment.format('YYYY-MM-DD'),
            fromDate: fromDateTimeMoment.format('YYYY-MM-DD'),
            fromTime: fromDateTimeMoment.format('HH:mm'),
            toDate: toDateTimeMoment ? toDateTimeMoment.format('YYYY-MM-DD') : null,
            toDisplayStr: '',
            tripType,
            toTime: toDateTimeMoment ? toDateTimeMoment.format('HH:mm') : null,
            twoDates: !!toDateTimeMoment,
        };

        this.modalService.dismiss({ newDates: data });
    }

    public onCloseModal(): void {
        this.modalService.dismiss();
    }

    private setDefaultValue(): void {
        if (this.defaultValue && !this.defaultValue.fromDate) {
            this.calendarDefaultValue = [new Date(), null];
            return;
        }

        if (this.defaultValue) {
            const fromDateTime = moment(this.defaultValue?.fromDate + ' ' + this.defaultValue.fromTime);
            const toDateTime = moment(this.defaultValue?.toDate + ' ' + this.defaultValue.toTime);

            this.calendarDefaultValue = [fromDateTime];

            if (toDateTime) {
                this.calendarDefaultValue.push(toDateTime);
            }

            this.selectedDateTimeArr = this.calendarDefaultValue;
        }
    }

    private setLang(): void {
        if (this.settingsService.i18nCode === 'en-us') {
            this.locale = localeEn;
        }

        if (this.settingsService.i18nCode === 'en-gb') {
            this.locale = localeEnGB;
        }

        if (this.settingsService.i18nCode === 'fr-fr') {
            this.locale = localeFr;
        }
    }

    private langChange(): void {
        this.settingsService.selectedLanguageSubject.pipe(takeUntil(this.destroy$)).subscribe(() => {
            this.setLang();
        });
    }

    private setMinMaxDate(): void {
        const calendarConf = this.calendarConf;
        const search = this.search;

        if (!calendarConf.linked) {
            return;
        }

        for (const dep in calendarConf.linked) {
            if (search[calendarConf.linked[dep]]) {
                switch (dep) {
                    case 'fromDate':
                        if (search[calendarConf.linked[dep]].fromDate) {
                            this.minDate = new Date(search[calendarConf.linked[dep]].fromDate);
                        }
                        break;
                    case 'toDate':
                        if (search[calendarConf.linked[dep]].toDate) {
                            this.maxDate = new Date(search[calendarConf.linked[dep]].toDate);
                        }
                        break;
                }
            }
        }
    }
}
